.App {
    width: 100%;
    height: 100vh;
    position: relative;
    overflow: hidden;
    display: flex;
}
.background {
    position: absolute;
    background: url(./images/background.jpg) no-repeat center center/cover;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
}